<template>
	<div id="member-list">
		<public-header title="顾客列表"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-notab">
				<van-search placeholder="请输入用户名/手机号/UID" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="member-list"
			>
				<div class="member-item" v-for="(item, index) in records.data" :key="item.id">
					<van-row>
						<van-col span="4">
							<div class="member-img">
								<img class="img-100" :src="item.avatar" alt="">
							</div>
						</van-col>
						<van-col span="20">
							<div class="member-right">
								<div class="member-title flex-lr">
									<div class="flex member-title-left ellipsis">
										<div class="member-name">{{item.nickname}}</div>
										<div class="c-default font-15">UID:{{item.uid}}</div>
									</div>
									<div class="flex">
										<div class="itag itag-primary" v-if="item.openid">公众号</div>
										<div class="itag itag-info margin-10-l" v-if="item.openid_wxapp">小程序</div>
									</div>
								</div>
								<div class="member-info flex">
									<div class="member-price">账户余额：<span class="c-danger">￥{{item.credit2}}</span></div>
									<div class="font-14 c-gray">积分余额：<span class="c-danger">{{item.credit1}}</span></div>
								</div>
								<div class="member-info flex">
									<div>成功下单：{{item.success_num}}次/{{item.success_price}}元</div>
								</div>
								<div class="member-info flex">
									<div>取消下单：{{item.cancel_num}}次/{{item.cancel_price}}元</div>
								</div>
								<div class="member-info flex" v-if="item.success_first_time != 0">
									<div>首次下单时间：{{item.success_first_time_cn}}</div>
								</div>
								<div class="member-info flex" v-if="item.success_last_time != 0">
									<div>最近一次下单时间：{{item.success_last_time_cn}}</div>
								</div>
								<div class="member-info flex" v-if="item.card">
									<div>套餐类型：</div>
									<div class="member-status itag itag-danger">{{item.card}}</div>
								</div>
								<template v-if="item.setmeal_starttime != 0 && item.setmeal_endtime != 0">
									<div class="member-info flex">
										<div>套餐时间：</div>
										<div class="member-status itag itag-danger">{{item.setmeal_starttime_cn}}~{{item.setmeal_endtime_cn}}</div>
									</div>
									<div class="member-info flex">
										<div>套餐状态：</div>
										<div class="member-status itag" :class="{'itag-primary': item.setmeal_status == '未到期', 'itag-disabled':  item.setmeal_status == '已到期'}">{{item.setmeal_status}}</div>
									</div>
								</template>
							</div>
						</van-col>
					</van-row>
					<div class="border-1px-t btn-group">
						<router-link :to="util.getUrl({path: 'pages/member/change', query: {uid: item.uid}})" tag="div" class="btn-item bg-info">账户变动</router-link>
						<div class="btn-item bg-danger" v-if="item.status == 0" @click="onChangeStatus(item, 1, index)">移出黑名单</div>
						<div class="btn-item bg-default" v-if="item.status == 1" @click="onChangeStatus(item, 0, index)">加入黑名单</div>
						<div class="btn-item" @click="onDeleteMember(item, index)">删除</div>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
				filter: {
					items: {}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/member/list/list'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onChangeStatus(item, status, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/member/list/status',
					data: {
						id: item.id,
						status: status
					},
					confirm: '确定加入黑名单吗？',
					success:function(){
						that.records.data[index].status = status;
					}
				});
			},
			onDeleteMember(item, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/member/list/del',
					data: {
						id: item.id,
						uid: item.uid
					},
					confirm: '确定删除吗？',
					success:function(){
						that.records.data.splice(index, 1);
					}
				});
			}
		},
		mounted(){
			this.onLoad();
		},

		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#member-list .content .van-search{
		padding: 10px;
	}
	#member-list .content .member-item{
		background-color: #fff;
		margin-bottom: 10px;
	}
	#member-list .content .member-item .member-img{
		width: 45px;
		height: 45px;
		padding-top: 10px;
		padding-left: 10px;
	}
	#member-list .content .member-item .member-img img{
		width: 100%;
		height: 100%;
		border-radius: 100%;
	}
	#member-list .content .member-item .member-right{
		position: relative;
		padding-top: 10px;
		padding-right: 10px;
	}
	#member-list .content .member-item .member-right .member-title{
		height: 35px;
	}
	#member-list .content .member-item .member-right .member-title .member-title-left{
		max-width: 52%;
	}
	#member-list .content .member-item .member-right .member-title .member-name{
		font-weight: bold;
		padding-right: 10px;
		color: #333;
	}
	#member-list .content .member-item .member-right .member-info{
		font-size: 14px;
		color: #7a7a7a;
		padding-bottom: 8px;
	}
	#member-list .content .member-item .member-right .member-info .member-price{
		font-size: 14px;
		color: #7a7a7a;
		padding-right: 10px;
	}
	#member-list .content .member-item .member-right .member-info:last-child{
		padding-bottom: 15px;
	}
	#member-list .content .member-item .member-right .member-info .member-status{
		font-size: 12px;
		padding: 2px 3px;
		margin-left: 5px;
	}
	#member-list .content .member-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#member-list .content .member-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
	#member-list .content .member-item .btn-group .bg-default{
		border: 1px solid #ccc;
	}
	#member-list .content .member-item .btn-group div:last-child{
		background-color: #878787;
		color: #fff;
		border: none;
	}
</style>


